import React, { Component } from 'react';
import { Row, Col, Form, Input, Select, DatePicker, Checkbox } from 'antd';
import FilterPathEducate from 'components/common/input/FilterPathEducate';
import YearSemesterInput from 'components/common/input/YearSemesterInput';
import SelectAjax from 'components/common/input/SelectAjax';
import SelectMultiAjax from 'components/common/input/SelectMultiAjax';
import { requiredRule } from 'utils/validateRules';
import { getTestSessions } from 'services/testSession';
import { searchSubject } from 'services/classCredit';

const { Option } = Select;
const optionFieldDecorator = {
  semesterId: {
    rules: [requiredRule],
  },
  yearEduId: {
    rules: [requiredRule],
  },
};

class FormCreateInfoCommon extends Component {
  state = {};

  componentWillReceiveProps = nextProps => {
    if (nextProps.testOrgValue !== this.props.testOrgValue) {
      this.props.form.setFieldsValue(nextProps.testOrgValue);
    }
  };
  handleReset = () => {
    this.props.form.resetFields();
  };

  render() {
    const { getFieldDecorator, getFieldValue } = this.props.form;
    const trainTypeId = getFieldValue('trainTypeId');
    const facultyId = getFieldValue('facultyId');
    const courseId = getFieldValue('courseId');
    const studyFieldId = getFieldValue('studyFieldId');
    const yearEduId = getFieldValue('yearEduId');
    const semesterId = getFieldValue('semesterId');
    return (
      <Form className="form-inline-label" layout="vertical" onSubmit={this.handleSearch}>
        <Row gutter={24}>
          <FilterPathEducate form={this.props.form} />
        </Row>
        <Row gutter={24}>
          <YearSemesterInput
            optionFieldDecorator={optionFieldDecorator}
            span={5}
            form={this.props.form}
          />
          <Col span={4}>
            <Form.Item label="Lần thi">
              {getFieldDecorator('examTime', { initialValue: '' })(
                <Select allowClear>
                  {[1, 2, 3, 4].map(i => {
                    return <Option value={i}>{i}</Option>;
                  })}
                </Select>
              )}
            </Form.Item>
          </Col>
          <Col span={4}>
            <Form.Item label="Đợt thi">
              {getFieldDecorator('examBatch', { initialValue: '' })(
                <Select allowClear>
                  {[1, 2, 3, 4].map(i => {
                    return <Option value={i}>{i}</Option>;
                  })}
                </Select>
              )}
            </Form.Item>
          </Col>
          <Col span={4}>
            <Form.Item label="Ca thi">
              {getFieldDecorator('testSessionId', { initialValue: '' })(
                <SelectAjax
                  setLabel={item => {
                    let label = item.label.replace('Ca ', '');
                    label = label[0].toUpperCase() + label.substring(1);
                    return label;
                  }}
                  service={getTestSessions}
                />
              )}
            </Form.Item>
          </Col>
        </Row>
        <Row gutter={24}>
          <Col span={8}>
            <Form.Item label="Ngày giờ thi" hasFeedback>
              {getFieldDecorator('testDate', { rules: [requiredRule] })(<DatePicker showTime />)}
            </Form.Item>
          </Col>
          <Col span={4}>
            <Form.Item label="Tiết" hasFeedback>
              {getFieldDecorator('fromSPId', { rules: [requiredRule] })(
                <Input style={{ width: 50 }} />
              )}
              {'=>'}
              {getFieldDecorator('toSPId', { rules: [requiredRule] })(
                <Input style={{ width: 50 }} />
              )}
            </Form.Item>
          </Col>
          <Col span={12}>
            <Form.Item label="Học phần">
              {getFieldDecorator('subjectIds', {
                rules: [requiredRule],
              })(
                <SelectMultiAjax
                  keyValue="subjectId"
                  keyLabel="subjectLabel"
                  service={searchSubject}
                  allowGetData={!!semesterId}
                  params={{ trainTypeId, facultyId, courseId, studyFieldId, yearEduId, semesterId }}
                  form={this.props.form}
                />
              )}
            </Form.Item>
          </Col>
        </Row>
        <Col span={4}>
          <Form.Item className="form-item-checkbox" label="SV đủ ĐK thi">
            {getFieldDecorator('ignoreNotQualify', {
              valuePropName: 'checked',
            })(<Checkbox />)}
          </Form.Item>
        </Col>
      </Form>
    );
  }
}

export default Form.create({
  name: 'FormSearchStudent',
  onValuesChange: (props, changedValues, allValues) => {
    console.log(props, allValues);
    if (props.getValuesChange) {
      props.getValuesChange(allValues);
    }
  },
})(FormCreateInfoCommon);



